import { useEffect, useRef, useState } from 'react';
import { componentWithApiProps } from '../components/componentDefine';
import { guid8 } from '../extensions/HashFuncs';
import thisCss from './EstimateWorkLogs.module.scss';
import { navigate } from '../components/router/methods';
import { getAsync, postAsync } from '../extensions/HttpExtension';
import PagingTableV2 from '../components/forms/table/PagingTableV2';
import { dataResponse, rowProps } from '../components/forms/table/PagingTable';
import ToolStripMenu from '../components/forms/toolstrip/ToolStripMenu';
import ToolStripItem from '../components/forms/toolstrip/ToolStripItem';
import { closeProcessing, ConfirmButtons, DialogOptions, openDialog, openProcessing } from '../components/forms/dialog/Dialog';
import { getElementById } from '../extensions/ElementExtension';

interface storageProps {
    AssigneeId?: string,
    Tasks?: any[],
    Total?: number,
    Sprint?: sprintProps
}
interface taskProps {
    Key?: string,
    Status?: string,
    StoryPoints?: number,
    Summary?: string,
    Tasks?: taskProps[]
}
interface WorkDayProps {
    Key?: string,
    Date?: string,
    Points?: number,
    Time?: string
}
interface sprintProps {
    WorkDayHours?: WorkDayProps[]
}

function EstimateWorkLogs(props?: componentWithApiProps & {
    headers: any,
    sprint: any,
    project: any,
    accountId?: string,
    onFinish?: () => void
}) {
    const thisId = props.id ?? guid8();

    const jAccountId = props.accountId ?? sessionStorage.getItem('j-accountid');
    const jUrl = localStorage.getItem('j-url');

    const now = new Date();
    const timezoneOffset = now.getTimezoneOffset();
    const [tableKey, setTableKey] = useState<string>(undefined);
    const workTimeRef = useRef<WorkDayProps[]>(undefined);
    const beginTimeRef = useRef<string>("08:30");
    const processingId = `${thisId}-processing`;

    useEffect(() => {
        if (!jAccountId) {
            navigate('/jira/login');
        }

        setTableKey(guid8());

    }, []);

    function fetchData() {
        const url = `${props.apiUrl}/api/jira/getusageinsprint?jAccountId=${jAccountId}&sprintId=${props.sprint?.id}`;
        return getAsync(url, props.headers, async response => {
            const result: dataResponse = {};
            if (response.status == 200) {
                const usageObjs: storageProps[] = await response.json();
                if (usageObjs?.length > 0) {
                    var usageObj = usageObjs[0];

                    if (usageObj?.Tasks?.length > 0) {

                        workTimeRef.current = usageObj.Sprint.WorkDayHours;

                        const records: rowProps[] = [];
                        Array.from<taskProps>(usageObj.Tasks).forEach((mainTask, mainIdx) => {
                            const mainRow: rowProps = {
                                id: `mt-${mainIdx}`,
                                className: thisCss["EstimateWorkLogs-list-ptask"],
                                cells: [
                                    { element: <div>{mainIdx + 1}</div>, align: "center" },
                                    { element: <div>{mainTask.Key}</div>, align: "center" },
                                    { element: <div>{mainTask.Summary}</div> },
                                    { element: <div>{mainTask.StoryPoints}</div>, align: "center" },
                                    { element: <div></div>, align: "center" },
                                ],
                            };

                            if (mainTask.Tasks?.length > 0) {
                                records.push(mainRow);
                                Array.from<taskProps>(mainTask.Tasks).forEach((subTask, subIdx) => {

                                    const subRow: rowProps = {
                                        id: `mt-${mainIdx}-${subIdx}`,
                                        className: `${thisCss["EstimateWorkLogs-list-task"]} ${thisCss["EstimateWorkLogs-list-task-active"]}`,
                                        cells: [
                                            { element: <div>{mainIdx + 1}-{subIdx + 1}</div>, align: "center" },
                                            { element: <div>{subTask.Key}</div>, align: "center" },
                                            { element: <div>{subTask.Summary}</div> },
                                            { element: <div>{subTask.StoryPoints}</div>, align: "center" },
                                            { element: <div>-</div>, align: "center" }
                                        ],
                                    };
                                    records.push(subRow);
                                });
                            }
                            else {
                                mainRow.className += ` ${thisCss["EstimateWorkLogs-list-task-active"]}`;
                                records.push(mainRow);
                            }
                        });
                        result.records = records;
                    }
                }
            }
            return result;
        });
    }

    function logTimeClick(id?: string) {
        alert(id);
    }

    function autoEstimateWorkLogs() {
        const taskClassName = thisCss["EstimateWorkLogs-list-task-active"];
        const rowObjs = document.getElementsByClassName(taskClassName) as HTMLCollectionOf<HTMLTableRowElement>;
        const dateLogs = workTimeRef.current;
        dateLogs.forEach(ite => ite.Points = 4);
        if (rowObjs?.length > 0 && dateLogs?.length > 0) {
            let dateIdx = 0;
            let dateLog: WorkDayProps;
            Array.from(rowObjs).forEach(rowObj => {
                const taskWorkLogObjs: WorkDayProps[] = [];
                const key = rowObj.cells[1].innerText;
                const sps = parseFloat(rowObj.cells[3].innerText);
                let remainSps = sps;
                while (remainSps > 0 && dateIdx < dateLogs.length) {
                    dateLog = dateLogs[dateIdx];
                    let points = remainSps >= dateLog.Points ? dateLog.Points : dateLog.Points - remainSps;
                    taskWorkLogObjs.push({
                        Key: key,
                        Date: dateLog.Date,
                        Points: points,
                        Time: `${points * 2}h`
                    });
                    remainSps -= points;
                    dateLog.Points -= points;
                    if (dateLog.Points == 0) {
                        dateIdx++;
                    }
                }
                const logTimeValue = taskWorkLogObjs.map((taskLog, idx) => `<div task-id="${thisId}-${key}" class="${thisCss["EstimateWorkLogs-list-task-active-div"]}" id="${thisId}-${key}-div-active-${idx}"><input checked task-key="${key}" id="${thisId}-${key}-div-active-${idx}-chk" class="${thisCss["EstimateWorkLogs-list-task-active-div-chk"]}" type="checkbox" value="${taskLog.Time} (${taskLog.Date} ${beginTimeRef.current})" /><span>${taskLog.Time} (${taskLog.Date} ${beginTimeRef.current})</span></div>`).join('\n');
                rowObj.cells[4].innerHTML = logTimeValue;
            });
            var logTimeDivObjs = document.getElementsByClassName(thisCss["EstimateWorkLogs-list-task-active-div"]) as HTMLCollectionOf<HTMLDivElement>;
            if (logTimeDivObjs?.length > 0) {
                Array.from(logTimeDivObjs).forEach(ite => {
                    ite.addEventListener("click", (evt) => {
                        if (evt.ctrlKey) {
                            const taskId = ite.getAttribute('task-id');
                            const chkClassName = thisCss["EstimateWorkLogs-list-task-active-div-chk"];
                            let chkObjs = document.getElementsByClassName(chkClassName) as HTMLCollectionOf<HTMLInputElement>;
                            if (chkObjs?.length > 0) {
                                const taskChkObjs = Array.from(chkObjs).filter(chkItem => chkItem.id.startsWith(`${taskId}-`));
                                if (taskChkObjs?.length > 0) {
                                    const firstChkChecked = taskChkObjs[0].checked;
                                    taskChkObjs.forEach(chkItem => chkItem.checked = !firstChkChecked);
                                }
                            }
                        }
                        else {
                            const chkId = `${ite.id}-chk`;
                            const chk = getElementById(chkId) as HTMLInputElement;
                            if (chk) {
                                chk.checked = !chk.checked;
                            }
                        }

                    });
                });
            }

        }
    }

    function onUpdateWorkLogs() {
        const dUpdateStory: DialogOptions = {
            zIndex: 2,
            id: guid8(),
            body: (<div className={thisCss["List-dialog-updateworklogs"]}>
                Are you sure?
            </div>),
            title: 'Update storages',
            confirmButtons: ConfirmButtons.YesNo,
            onAcceptHandler: updateWorkLogs
        };
        openDialog(dUpdateStory);
    }
    async function updateWorkLogs() {
        const chkClassName = thisCss["EstimateWorkLogs-list-task-active-div-chk"];
        const chkObjs = document.getElementsByClassName(chkClassName) as HTMLCollectionOf<HTMLInputElement>;
        if (chkObjs?.length > 0) {
            const workLogObjs: any[] = [];
            Array.from(chkObjs).forEach(ite => {
                if (ite.checked) {
                    const key = ite.getAttribute('task-key');
                    const logTimeValue = ite.value;
                    workLogObjs.push({
                        issueKey: key,
                        logTimes: logTimeValue.split('\n')
                    });
                }

            });
            if (workLogObjs?.length > 0) {
                const requestUrl = `${props.apiUrl}/api/jira/bulkupdateissueworklogs?timezoneOffset=${timezoneOffset}`;
                openProcessing(processingId);
                await postAsync(requestUrl, workLogObjs, props.headers, async response => {
                    const dUpdateWorklogFinish: DialogOptions = {
                        zIndex: 3,
                        id: guid8(),
                        body: (<div className={thisCss["List-dialog-updateworklogs"]}>
                            Update finish.
                        </div>),
                        title: 'Update work logs',
                        confirmButtons: ConfirmButtons.Ok,
                        onAcceptHandler: () => {
                            if (props.onFinish) { props.onFinish(); }
                        }
                    };
                    openDialog(dUpdateWorklogFinish);
                }, undefined, () => closeProcessing(processingId));
            }

        }
    }

    function setBeginTime(time: string) {
        beginTimeRef.current = time;
        const spanBeginTime = getElementById(`${thisId}-begintime`) as HTMLSpanElement;
        if (spanBeginTime) {
            const childSpan = spanBeginTime.children[0] as HTMLSpanElement;
            if (childSpan) {
                childSpan.innerHTML = `Begin Time: ${beginTimeRef.current}`;
            }
        }
        autoEstimateWorkLogs();
    }

    let className = thisCss["EstimateWorkLogs"];
    if (props?.className) {
        className += ` ${props.className}`;
    }
    try {

        const menuElement: JSX.Element = (<div>
            <ToolStripMenu>
                <ToolStripItem text='Menu' />
                <ToolStripItem text='Set Begin Time' >
                    <ToolStripItem text='07:30' onClick={() => setBeginTime('07:30')} />
                    <ToolStripItem text='08:00' onClick={() => setBeginTime('08:00')} />
                    <ToolStripItem text='08:30' onClick={() => setBeginTime('08:30')} />
                    <ToolStripItem text='09:00' onClick={() => setBeginTime('09:00')} />
                    <ToolStripItem text='09:30' onClick={() => setBeginTime('09:30')} />
                </ToolStripItem>

                <ToolStripItem text='Auto' onClick={autoEstimateWorkLogs} />
                <ToolStripItem text='Update' onClick={onUpdateWorkLogs} />
                <ToolStripItem id={`${thisId}-begintime`} text={`Begin Time: ${beginTimeRef.current}`} className={thisCss["EstimateWorkLogs-list-menu-begintime"]} />
            </ToolStripMenu>
        </div>);

        const tHeaders = ["1. No.", "2. Key", "3. Summary", "4. Story Points", "5. Work Log"];
        let contents = (<div id={thisId} className={className}>
            {menuElement}
            <PagingTableV2
                className={thisCss["EstimateWorkLogs-list"]}
                key={tableKey}
                headers={tHeaders}
                fetchData={fetchData}
                noPaging
            />

        </div>);
        return contents;
    }
    catch (e) {
        console.log(e);
    }
}
export default EstimateWorkLogs;